import React from "react";
import Tabletop from 'tabletop';
import Timeline from './Timeline.js';
import { Container } from 'react-bootstrap';

class Home extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      data: [],
      dataReady: 0
    };
    this.handleData = this.handleData.bind(this);
  }

  handleData = () => {
    this.setState({
      dataReady: this.state.dataReady + 1
    })
  }

  componentDidMount() {
    setTimeout(function () {
      this.props.loadingBarAdd(20);
    }.bind(this),100);

    Tabletop.init({
      key: '1CNnSjBUnSRoT_dW5aJwKmzbWqf3_80PhM9RNBMYfgM8',
      orderby: 'date',
      callback: googleData => {
        this.setState({
          data: googleData
        });
        this.props.loadingBarComplete();
        this.handleData();
      },
      simpleSheet: true
    });
  }

  render() {
    const { data } = this.state
    return (
      <Container>

        <Timeline data={this.state.data} events={this.props.events} ready={this.state.data.length > 0 && this.props.events.length > 0 ? true : false} />

      </Container>
    );
  }
}

export default Home;
