import React from "react";
import Tabletop from 'tabletop';
import { Route, Link } from "react-router-dom";
import { HashRouter } from 'react-router-dom';
import Header from "./Header.js";
import Footer from "./Footer.js";
import Home from "./Home.js";
import About from "./About.js";
import LoadingBar from 'react-top-loading-bar'

export default class App extends React.Component {
  constructor() {
    super();
    this.state = {
      loadingBarProgress: 0,
      scrollDown: 0,
      events: []
    }
    this.handleScroll = this.handleScroll.bind(this);
  }

  componentDidMount() {
    window.addEventListener('scroll', this.handleScroll);

    Tabletop.init({
      key: '1QClBiSbsZKGlzugA1_CUKE8uI9-0PNDb9vkCmrEka3M',
      callback: googleData => {
        this.setState({
          events: googleData
        });
      },
      simpleSheet: true
    });
  }

  handleScroll = () => {
    let supportPageOffset = window.pageXOffset !== undefined;
    let isCSS1Compat = ((document.compatMode || '') === 'CSS1Compat');
    let scroll = {
       x: supportPageOffset ? window.pageXOffset : isCSS1Compat ? document.documentElement.scrollLeft : document.body.scrollLeft,
       y: supportPageOffset ? window.pageYOffset : isCSS1Compat ? document.documentElement.scrollTop : document.body.scrollTop
    };
    this.setState({
      scrollDown: scroll.y
    });
  }

  loadingBarAdd = value => {
    this.setState({
      loadingBarProgress: this.state.loadingBarProgress + value
    })
  }
  loadingBarComplete = () => {
    this.setState({ loadingBarProgress: 100 })
  }
  onLoaderFinished = () => {
    this.setState({ loadingBarProgress: 0 })
  }

  render() {
    return (
      <HashRouter>
        <div className={"wrapper" + (this.state.scrollDown > 20 ? ' scroll-down' : '') + (this.state.scrollDown >= window.document.body.offsetHeight - window.innerHeight ? ' reach-bottom' : '')}>
          <Header events={this.state.events} />

          <main>
            <Route exact path="/" render={(props) => <Home {...props} loadingBarAdd={this.loadingBarAdd} loadingBarComplete={this.loadingBarComplete} events={this.state.events} />} />
            <Route path="/about" render={(props) => <About {...props} loadingBarAdd={this.loadingBarAdd} loadingBarComplete={this.loadingBarComplete} />} />
          </main>

          <Footer />
        </div>
      </HashRouter>
    )
  }
}

function Topic({ match }) {
  return <h3>Requested Param: {match.params.id}</h3>;
}

function Topics({ match }) {
  return (
    <div>
      <h2>Topics</h2>

      <ul>
        <li>
          <Link to={`${match.url}/components`}>Components</Link>
        </li>
        <li>
          <Link to={`${match.url}/props-v-state`}>Props v. State</Link>
        </li>
      </ul>

      <Route path={`${match.path}/:id`} component={Topic} />
      <Route
        exact
        path={match.path}
        render={() => <h3>Please select a topic.</h3>}
      />
    </div>
  );
}
