import React from "react";
import { Link } from "react-router-dom";
import { Container } from 'react-bootstrap';
import Spinner from './Spinner.js';

class Header extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      data: []
    };
  }

  render() {
    return (
      <div className="header">
        <Container>
          <div className="h-1">
            <div className="h-logo">
              <h1><a href="/">Free HK</a></h1>
            </div>
            <ul className="nav">
              <li>
                <Link to="/">Timeline</Link>
              </li>
              <li>
                <Link to="/">Events</Link>
                <Submenu items={this.props.events} />
              </li>
              <li>
                <Link to="/about">About</Link>
              </li>
              <li>
                <Link to="/download">Download</Link>
              </li>
            </ul>
          </div>
        </Container>
      </div>
    );
  }
}

function Submenu(props) {
  if(props.items.length > 0){
    return (
      <div className="submenu">
        <ul className="nav">
        {
          props.items.map(obj => {
            return (
              <li key={obj.id}>
                <Link to={"events/"+obj.title.replace(/\s\/\s/g, "-")}>{obj.title}</Link>
              </li>
            )
          })
        }
        </ul>
      </div>
    )
  }
  return (
    <div className="submenu text-center p-4">
      <Spinner />
    </div>
  )
}

export default Header;
